import {
	userFetchCheckCode,
	userBindAccount,
} from './apis';

const app = getApp();

export default {
	data() {
		return {
			fetchQrCodeButtonDisable: false,
			countDown: 59,
			userPhoneNumber: '',
			userVerCode: '',
		}
	},
	methods: {
		// 用户获取验证码
		handleUserFetchCheckCode() {
			if (this.fetchQrCodeButtonDisable) return;
			
			if (!this.userPhoneNumber) {
				uni.showToast({
					title: '请输入手机号',
					icon: 'none',
				}); 
				return
			}
							
			userFetchCheckCode(app.globalData.openid, this.userPhoneNumber)
				.then((res) => {
					this.fetchQrCodeButtonDisable = true;
					this.countDownInt = setInterval(() => {
						if (!this.countDown) {
							this.countDown = 60;
							this.fetchQrCodeButtonDisable = false;
							clearInterval(this.countDownInt);
							return;
						}
						this.countDown -= 1;
					}, 1000)
				})
		},
		// 用户绑定账号
		handleUserBindAccount() {
			userBindAccount(
				app.globalData.openid,
				this.userPhoneNumber,
				this.userVerCode,
			)
				.then((res) => {
					uni.redirectTo({ url: '/pages/index/index' });
				})
		},
	}
}
